<template>
    <div>
        <z-header></z-header>
        <div>
            <van-swipe :autoplay="3000" indicator-color="white">
                <van-swipe-item>
                    <a href="">
                        <img src="../../assets/banner/banner-img2.jpg" alt="">
                    </a>
                </van-swipe-item>
                <van-swipe-item>
                    <a href="">
                        <img src="../../assets/banner/banner-img3.jpg" alt="">
                    </a>
                </van-swipe-item>
            </van-swipe>
        </div>
        <div class="jingang">
            <ul class="jingang-list">
                <li class="jingang-item">
                    <a href="">
                        <div class="img">
                            <img src="../../assets/jingang/foot.png" alt="">
                        </div>
                        <div class="jingang-text">水果特惠</div>
                    </a>
                </li>
                <li class="jingang-item">
                    <a href="">
                        <div class="img" style="background-color: #E6C3BD">
                            <img src="../../assets/jingang/today.png" alt="">
                        </div>
                        <div class="jingang-text">每日必抢</div>
                    </a>
                </li>
                <li class="jingang-item">
                    <a href="">
                        <div class="img" style="background-color: #EED0B8">
                            <img src="../../assets/jingang/fuli.png" alt="">
                        </div>
                        <div class="jingang-text">嗨吃福利</div>
                    </a>
                </li>
                <li class="jingang-item">
                    <a href="">
                        <div class="img" style="background-color: #8CB2C5">
                            <img src="../../assets/jingang/good.png" alt="">
                        </div>
                        <div class="jingang-text">鲜蜂力荐</div>
                    </a>
                </li>
            </ul>
        </div>
        <div v-if="goods">
            <div class="youxuan" v-for="cate in goods" :key="cate.id">
                <div class="temai-text">
                    <span>
                        <router-link :to="{name:'list',query:{id:cate.id}}">{{cate.cname}}</router-link>
                    </span>
                    <div>
                        <img src="../../assets/jingang/arrow-right2.png" alt="更多">
                    </div>
                </div>
                <div class="temai-img">
                    <div class="img-item">
                        <img :src="imgName+cate.thumb" alt="">
                    </div>
                </div>
                <ul class="furit-list">
                    <li class="fruit-item" v-for="item in cate.goods" :key="item.id">
                        <router-link :to="{name:'details',query:{id:item.id}}">
                            <div class="fruit-img">
                                <img :src="imgName+item.thumb" alt="item.gname">
                            </div>
                            <div class="fruit-title">{{item.gname}}</div>
                            <div class="zeng"><span>买一赠一</span></div>
                            <div class="fruit-price">￥{{item.sale}}
                                <del style="font-size: 12px;color: #979797; margin-left:0.04rem;">￥{{item.mprice}}</del>
                            </div>
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
        <div style="height: 1rem;"></div>
        <z-footer :active="1"></z-footer>
    </div>
</template>

<script>
    import ZHeader from '../../components/z-header/z-header';
    import ZFooter from '../../components/z-footer/z-footer';
    import {SUCCESS, IMGPATH,HOSTNAME} from "../../config/base";

    export default {
        name: "Index",
        components: {
            ZHeader,
            ZFooter
        },
        data() {
            return {
                imgName: IMGPATH,
                goods: [],
            }
        },
        methods: {
            categoods() {
                fetch(HOSTNAME+'/api/index', {
                    method: 'GET',
                })
                    .then(res => res.json())
                    .then(data => {
                        if (data.code === SUCCESS) {
                            this.goods = data.data;
                        } else {
                            window.console.log(data.msg);
                        }
                    })
            }
        },
        beforeMount() {
            this.categoods();
        }
    }
</script>

<style scoped>
    @import url("../../style/index.css");

    a {
        color: black;
    }

    .van-swipe {
        margin-top: 1.3rem;
        height: 4rem;
    }

    .van-swipe-item > a {
        width: 100%;
        height: 4rem;
        display: block;
    }

    .van-swipe-item > a > img {
        width: 100%;
        height: 100%;
    }
</style>
